
.top{
  margin-bottom:20px;
}
.home_img{
  width:100%;
  min-height: 200px;
  max-height: 250px
}
.home_box{
  padding: 0 14px;
}
.home_title{
  line-height: 50px;
  font-family: PingFang-SC;
  font-size: 16px;
  font-weight: 500;
  text-align: left;
  color: #161817;
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;  
  -webkit-box-pack: justify;  
  -webkit-justify-content: space-between;  
          justify-content: space-between;
}
.home_link{
  border-radius:2px;
  margin-bottom:20px;
  display: block;
  border-radius: 4px;
  background-color: #FFF;
  box-shadow: 0 4px 4px 0 #e6e6e6;
}
.home_link_box{
  border-radius:2px;
  padding: 0 20px;
  background-color: #FFF;
  height: 100px;
  display: -webkit-box;
  display: flex;
  display: -webkit-flex;  
  -webkit-box-align:center;  
  -webkit-align-items:center;  
          align-items:center;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.home_link_left{
  font-size:30px;
}
.home_link_title_lab{
  font-family: PingFangSC;
  font-size: 12px;
  color: #161817;
}
.home_link_right{
  width: 24px;
  height: 24px;
  border-radius: 50%;
  text-align: center;
  line-height: 24px;
  color: #FFF
}
.home_link_title_tip{
  padding: 0 20px;
  line-height: 36px;
  color: #FFF;
}
.q_bk{
  background-color: #f6a623;
}
.t_bk{
  background-color: #0eb8a5;
}
.q{
  color:#f6a623;
}
.a{
  color: #00c88b;
}





