
li{list-style: none
}
.me_top{
  text-align:center;
  padding: 33px 0 20px 0;
  background-color: #83c7d4;
  color: #FFF;
}
.me_img{
  width:72px;
  height: 72px;
  border-radius: 50%
}
.me_nickname{
  margin-bottom: 22px;
}
.me_data{
  text-align: center;
}
.me_data_item{
  display: inline-block;
  width:40%;
}
.container{
  padding: 0 10px;
  padding-top: 20px;
}
.item{
  overflow: hidden;
  margin-bottom: 20px;
}
.item_block{
  overflow: hidden;
  padding: 8px 0;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc
}
.item_block img{
    float: left;
}
.item_title{
  text-align: left;
  margin-bottom: 10px;
}
.item_right{
  margin-left: 70px;
}
.vux-demo {
  text-align: center;
}
.logo {
  width: 100px;
  height: 100px
}
.sub-item {
  color: #888;
}
.slide {
  overflow: hidden;
  max-height: 0;
  -webkit-transition: max-height .5s cubic-bezier(0, 1, 0, 1) -.1s;
  transition: max-height .5s cubic-bezier(0, 1, 0, 1) -.1s;
}
.animate {
  max-height: 9999px;
  -webkit-transition-timing-function: cubic-bezier(0.5, 0, 1, 0);
          transition-timing-function: cubic-bezier(0.5, 0, 1, 0);
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}
.gray{
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  -webkit-filter: gray;
          filter: gray;
}
.page_me{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.me{
  padding: 0 19px;
  background-color: #fff;
}
.me_item{
  position: relative;
  line-height: 64px;
  box-shadow: inset 0 -1px 0 0 #e6e6e6;
  font-family: PingFangSC;
  font-size: 16px;
  color: #505050;
  background-color: #FFF;
}
.me_item:last-child{
  box-shadow: none;
}
.me_item_right{
    content: " ";
    display: inline-block;
    height: 6px;
    width: 6px;
    border-width: 2px 2px 0 0;
    border-color: #C8C8CD;
    border-style: solid;
    position: absolute;
    right: 19px;
    top: 50%;
    -webkit-transform:rotate(45deg) translate(0,-50%);
            transform:rotate(45deg) translate(0,-50%);
}
.border_none{
  box-shadow: none
}



