
@import '../styles/index.css';
.icon {
  width: 2em;
  height: 2em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.qrcode_down_wrap {
  height: 100vh;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  background-color: rgba(0,0,0,.3);
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
   -webkit-box-pack: center;
   -webkit-justify-content: center;
           justify-content: center
}
.qrcode_down_wrap p{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    text-align: center;
    background-color: rgba(0,0,0,.5);
    line-height: 30px;
    color: #FFF
}
input::-webkit-input-placeholder { /* WebKit browsers */
  color: #FFF;
  font-size: 16px;
}
input::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #FFF;
  font-size: 16px;
}
input::-ms-input-placeholder { /* Internet Explorer 10+ */
  color: #FFF;
}
.login_item {
  margin: 20px;
  height: 40px;
  overflow: hidden;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack:justify;
  -webkit-justify-content:space-between;
          justify-content:space-between;
}
.login_item img {
  width: 40px;
  height: 40px;
}
.login_item input {
  background: none;
  line-height: 40px;
  -webkit-appearance:none;
  border: none;
  font-size: 18px;
  padding-left: 20px;
  outline:none;
  color: #FFF;
}
.login_item input::-webkit-input-placeholder{
　　color: #FFF;
}
.login_item span {
  font-style: normal;
  line-height: 40px;
  font-size: 24px;
}



